



























































import Vue, { PropType } from "vue";
import { mapState, mapActions } from "vuex";
import { Editor, EditorContent } from "tiptap";
import {
  Bold,
  Italic,
  Strike,
  Underline,
  Code,
  Heading,
  Blockquote,
  CodeBlock,
  HorizontalRule,
} from "tiptap-extensions";

interface User {
  _id: string;
  name: string;
  createdAt: number;
}

interface Post {
  _id: string;
  title: string;
  body: string;
  user: string;
  points: number;
  sub: User;
  createdAt: number;
}

export default Vue.extend({
  name: "Post",
  components: {
    EditorContent,
  },
  props: {
    postData: Object as PropType<Post>,
  },
  data() {
    return {
      formatedTime: null,
      readOnlyEditor: new Editor({
        content: JSON.parse(this.postData.body),
        editable: false,
        editorProps: {
          attributes: {
            class: "p-4 position-inherit focus:outline-none",
          },
        },
        extensions: [
          new Bold(),
          new Italic(),
          new Strike(),
          new Underline(),
          new Code(),
          new Heading({ levels: [1, 2, 3] }),
          new Blockquote(),
          new CodeBlock(),
          new HorizontalRule(),
        ],
      }),
    };
  },
  computed: {
    ...mapState("DarkMode", ["isDarkMode"]),
  },
  methods: {
    ...mapActions("EventState", ["getTimeSince"]),
    handleClick() {
      this.$router.push(`/${this.postData._id}`);
    },
  },
  beforeDestroy() {
    this.readOnlyEditor.destroy();
  },
  mounted: async function() {
    this.formatedTime = await this.getTimeSince(this.postData.createdAt);
  },
});
