






























































































































import Vue from "vue";
import { mapState, mapActions } from "vuex";
import DarkModeToggle from "@/components/DarkModeToggle.vue";
import LoginModal from "@/components/LoginModal.vue";
import Sidebar from "@/components/Sidebar.vue";
import Error from "@/components/Error.vue";

export default Vue.extend({
  name: "Navbar",
  components: {
    DarkModeToggle,
    LoginModal,
    Sidebar,
    Error,
  },
  data: function() {
    return {
      scroll: document.documentElement.scrollTop,
      perfersDark: window.matchMedia("(prefers-color-scheme: dark)").matches,
    };
  },
  computed: {
    ...mapState("AuthState", ["isAuthenticated", "isAuthLoading"]),
    ...mapState("PostState", ["isPostLoading"]),
    ...mapState("SubState", ["isSubLoading"]),
    ...mapState("DarkMode", ["isDarkMode"]),
    ...mapState("MediaQueryState", ["mq"]),
    ...mapState("EventState", ["loginModal", "sidebar"]),
  },
  methods: {
    ...mapActions("AuthState", ["logout", "me"]),
    ...mapActions("DarkMode", ["turnOn"]),
    ...mapActions("EventState", [
      "openLoginModal",
      "closeLoginModal",
      "openSidebar",
      "closeSidebar",
    ]),
    handleLogout() {
      this.logout();
    },
  },
  mounted: function() {
    document.addEventListener(
      "scroll",
      () => (this.scroll = document.documentElement.scrollTop)
    );

    if (this.perfersDark) this.turnOn();

    this.me();
  },
});
